import React from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';

function TermsAndConditions() {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const returnTo = searchParams.get('returnTo');

  const handleAccept = () => {
    if (returnTo) {
      navigate(`/website-plans/${returnTo}`);
    } else {
      navigate('/home');
    }
  };

  return (
    <div className="min-h-screen p-6 bg-white text-gray-800">
      <h1 className="text-3xl font-bold mb-4">Conditions Gnrales dUtilisation</h1>

      <div className="space-y-4 text-sm leading-relaxed">
        <p>
          En utilisant les services de WinnerDream, vous acceptez les conditions suivantes. Ces
          conditions rgissent lutilisation de nos produits, abonnements et fonctionnalits.
        </p>

        <h2 className="text-xl font-semibold mt-6">1. Abonnements</h2>
        <p>
          Labonnement mensuel est renouvel automatiquement. Vous pouvez lannuler  tout moment
          via votre tableau de bord.
        </p>

        <h2 className="text-xl font-semibold mt-6">2. Paiement</h2>
        <p>
          Tous les paiements sont grs de manire scurise via Stripe. Aucune donne bancaire
          nest stocke sur nos serveurs.
        </p>

        <h2 className="text-xl font-semibold mt-6">3. Proprit</h2>
        <p>
          Vous restez propritaire de vos donnes. Si vous souscrivez un plan de site web, vous
          pouvez demander un transfert  tout moment.
        </p>

        <h2 className="text-xl font-semibold mt-6">4. Assistance</h2>
        <p>
          Notre quipe est disponible pour vous assister sur les aspects techniques et commerciaux
          via votre espace client.
        </p>

        <h2 className="text-xl font-semibold mt-6">5. Rsiliation</h2>
        <p>
          Vous pouvez mettre fin  votre abonnement  tout moment. Aucun remboursement nest effectu
          pour les priodes dj entames.
        </p>
      </div>

      <div className="mt-8 flex gap-4">
        <button
          onClick={handleAccept}
          className="bg-green-600 hover:bg-green-700 text-white px-6 py-2 rounded"
        >
           Jai lu et jaccepte
        </button>
        <button
          onClick={() => navigate(-1)}
          className="bg-gray-400 hover:bg-gray-500 text-white px-6 py-2 rounded"
        >
           Retour
        </button>
      </div>
    </div>
  );
}

export default TermsAndConditions;



